.loadingOverlay {
  height: 100vh;
}

.cartContainer {
  width: 90%; //  
  margin: 0px auto;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;

  .cartTitle {
    margin-bottom: 40px;
  }

  .bundleContainer {
    width: 900px;
    padding: 20px;
    margin-bottom: 15px; //    
    border-radius: 8px; //  
    background-color: #f4e8d3; //  
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.2s, box-shadow 0.2s; //  
  }

  .bundleHeader {
    font-size: 20px;
    font-weight: bold;
    color: #333;
    margin: 10px 0;
  }

  .treatsList {
    display: flex;
    flex-direction: column; //  
    width: 100%; //   100%
  }

  .treatItem {
    padding: 10px 50px 10px 10px;
    display: flex; //   
    align-items: center; //  
    border-bottom: 1px solid #ccc; //  

    //  
    img {
      width: 50px; //  
      height: auto; //  
      margin-right: 10px; //   
    }
  }

  .treatItem:last-child {
    border-bottom: none; //    
  }

  .bundleFooter {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 10px;
  }

  .bundlePrice {
    font-size: 16px; //   
    color: #333; //  
  }

  .itemRemove {
    font-size: 12px;
    color: #e74c3c; //   
    cursor: pointer;

    &:hover {
      text-decoration: underline; //    
    }
  }

  .totalPriceContainer {
    margin-top: 40px; //  
    width: 100%; //  100%
    border-top: 1px solid #ccc; //   
    padding-top: 20px; //  
  }
  .bundlePrice {
    color: #858585;
  }

  .priceRow {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px; //   
    font-size: 16px; //  
    color: #2c3e50; // 
  }

  .totalAmountRow {
    margin: 10px 0;
    display: flex;
    justify-content: space-between;
    font-size: 20px; //    
    font-weight: bold; // 
    color: #2c3e50; //  
  }

  .clearCartContainer {
    display: flex; // Flexbox 
    justify-content: flex-end; //  
  }

  .clearCartButton {
    color: black; // 
    background: none; //  
    border: none; //   
    border-bottom: 1px solid black; //   
    cursor: pointer; //  
    font-size: 12px; //  
    margin-left: auto; //   
    padding: 2px 0; //    (   )
  }

  .clearCartButton:hover {
    background-color: #cecbcb; //    
    opacity: 0.8;
  }

  .emptyCartContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 200px; //   
    width: 1000px; //  1000px    
    border-radius: 8px;
    padding: 30px; //  
    margin-top: 20px;
    margin-bottom: 60px;
    background-color: #f4e8d3; //    
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center; //   
  }

  .emptyCartIcon {
    font-size: 50px;
    color: #e74c3c;
    margin-bottom: 15px;
  }

  .emptyCartMessage {
    font-size: 20px; //   
    color: #555; //  
    margin-bottom: 15px; //   
  }

  .shopButton {
    padding: 10px 20px;
    background-color: #e88b4f; //  
    color: white;
    border: none;
    border-radius: 18px;
    cursor: pointer;
    font-size: 16px;
    transition: background-color 0.3s;

    &:hover {
      background-color: #d78b4f; //    
    }
  }

  .paymentButtonContainer {
    display: flex;
    justify-content: center; //   
    margin-top: 40px; //   
  }

  .paymentButton {
    width: 20%;
    padding: 10px 20px;
    margin-bottom: 40px;
    background-color: #218838; //  
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    transition: background-color 0.3s;

    &:hover {
      transform: scale(1.05); //    
    }
  }
}

.totalDisCount {
  margin-right: 5px;
  color: #bf424a;
  text-decoration: line-through; //  
}

.bundleHeaderContainer {
  display: flex;
  justify-content: space-between; //   
  align-items: center; //   
}

.subscriptionSelect {
  margin-right: 20px; // h4  
  padding: 5px; //  
  font-size: 14px; //   
  margin-top: 25px; //     
}

@media (max-width: 400px) {

  .bundleContainer {
    margin: 0 auto;
    width: 80% !important; //   
    padding: 10px; //  
    .bundleHeader{
      font-size: 16px;
    }
    .subscriptionSelect{
      margin-left: 10px;
      margin-right: 0px;
    }
  }

  .treatItem {
    padding: 5px 20px; //  
  }

  .emptyCartContainer {
    width: 80% !important; //  100%   
    padding: 20px; //  
  }

  .shopButton, .paymentButton {
    width: 50% !important; //   100% 
    padding: 10px; //   
    font-size: 12px; //   
  }
}
