:root {
  --padding-top: 90px;
  --padding-left: 130px;
  --img-section-display: flex; 
}

.shopMainBgContainer {
  .mainSection {
    height: 95vh;

    .imgSection {
      background-image: url(../../../public/shop_img/1.png); //   
      background-size: cover;
      background-position: center;
      height: 710px; /*     */
      width: 100%; /*     */
      position: relative; /*   relative  */

      .manageShopBtn {
        position: absolute; /*   absolute  */
        top: 10px; /*  10px   */
        right: 10px; /*  10px   */
      }
    }

    .sliderContainer {
      overflow: hidden;
      position: relative;
      height: 170px; //      
      background: #5e584f;
    }

    .slider {
      display: flex;
      animation: scroll 15s linear infinite; /*    */
      height: 100%; //    
      width: calc(
        250px * 5 * 2
      ); //     (  x    x 2)
    }

    .slideImgBox {
      flex: 0 0 auto;
      width: 260px;
      display: flex;
      justify-content: center; //   
      margin: auto 0;

      .slide {
        background-color: #5e584f;
        text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.7); //    
        height: 100%; //      

        &.slide1,
        &.slide2,
        &.slide3,
        &.slide4,
        &.slide5 {
          width: 55px; //   
          height: 54px; //   
          margin-right: 20px;
          background-size: cover; //    contain 
          background-position: center; //    
          background-repeat: no-repeat; //    
        }

        //      
        &.slide1 {
          background-image: url("../../assets/shop/.png");
        }

        &.slide2 {
          background-image: url("../../assets/shop/\ (1).png");
        }

        &.slide3 {
          background-image: url("../../assets/shop/\ (2).png");
        }

        &.slide4 {
          background-image: url("../../assets/shop/\ (3).png");
        }

        &.slide5 {
          background-image: url("../../assets/shop/\ (4).png");
        }

        @keyframes scroll {
          0% {
            transform: translateX(0);
          }
          100% {
            transform: translateX(-50%); /*     */
          }
        }
      }
    }

    .slideText {
      color: white;
      padding-top: 10px;
      margin-right: 10px; //      (  )
      font-size: 15px;
    }

    .imgSection2 {
      background-image: url(../../../public/shop_img/2.png); //   
      background-size: cover;
      background-position: center;
      height: 710px; /*     */
      width: 100%; /*     */
    }

    .imgSection3Box {
      display: flex;
    }

    .imgSection3 {
      float: left;
      background-image: url("../../../public/shop_img/3840x0.png"); //   
      background-size: cover; //      
      background-position: center; //    
      height: 710px; //   
      width: 50%; //   
      left: 0;
      display: var(--img-section-display); 
      flex-direction: column; //   
    }

    .processContainer {
      width: 50%;
      background-color: #e8e4d7; //  
      text-align: left;

      .content {
        margin-top: 40px;

        .guide {
          color: #827d76;
          width: 42%;
          margin-left: 35px;
        }

        .selectDogBox {
          display: flex; /* Flexbox  */
          width: 100%; /*     */

          .dogList {
            background: #e8e4d7;
            color: #403c36;
            width: 42%;
            margin-left: 35px;
          }
        }

        .startBtnBox {
          display: flex; /* Flexbox  */
          width: 100%; /*     */

          .startButton {
            width: 42%;
            background-color: #4d5d4c; //  
            padding: 18px;
            margin-top: 20px;
            margin-left: 35px;
            color: #fffff0;
            border: none;
            border-radius: 25px;
            cursor: pointer;
            font-size: 16px;
            font-weight: bold;
            transition: transform 0.4s ease-in-out; /*    */
          }

          .startButton:hover {
            transform: translateY(-3px); /*    5px   */
          }
        }
      }

      .processContent {
        padding-top: var(--padding-top);
        padding-left: var(--padding-left);
      }
      
      .processTitle {
        color: #403c36;
        font-size: 36px;
        font-weight: bold;
        margin-bottom: 50px;
      }

      .processStep {
        display: flex;
        margin-bottom: 20px;

        .stepNumber {
          font-size: 24px;
          font-weight: bold;
          padding-top: 5px;
          margin-right: 10px;
        }

        .stepContent {
          flex: 1;

          .stepIcon1 {
            background-image: url("../../../public/shop_img/dog.png"); /*    */
            background-size: contain; /*    */
            background-repeat: no-repeat; /*   */
            display: inline-block;
            margin-left: 10px;
            width: 40px;
            height: 40px;
            background-size: cover;
          }

          .stepIcon2 {
            background-image: url("../../../public/shop_img/big-idea.png"); /*    */
            background-size: contain; /*    */
            background-repeat: no-repeat; /*   */
            display: inline-block;
            margin-left: 10px;
            width: 42px;
            height: 40px;
            background-size: cover;
          }

          .stepIcon3 {
            background-image: url("../../../public/shop_img/gift.png"); /*    */
            background-size: contain; /*    */
            background-repeat: no-repeat; /*   */
            display: inline-block;
            margin-left: 10px;
            width: 42px;
            height: 40px;
            background-size: cover;
          }

          h5 {
            font-size: 17px;
            margin: 0;
            margin-bottom: 5px;
            color: #403c36;
          }

          p {
            margin: 0;
            margin-top: 10px;
            color: #403c36;
          }
        }
      }
    }

    .featuresContainer {
      display: flex;
      justify-content: center; /*    */
      gap: 150px; /*     */
      background-color: #5e5850; /*  */
      margin: 0 auto;
      padding: 50px 0; /*   */
      color: rgb(255, 255, 255); /*   */
    }

    .featureItem {
      text-align: center; /*    */
      max-width: 335px; /*    */

      .featureTitle {
        font-size: 20px;
        color: white;
        margin-top: 10px;
      }
      .featureContent {
        color: white;
      }
    }

    .subsIcon {
      background-image: url("../../../public/shop_img/subscribe1.png"); /*    */
      background-size: contain; /*    */
      background-repeat: no-repeat; /*   */
      margin: 0 auto;
      height: 70px; /*    */
      width: 70px; /*    */
    }

    .qualityIcon {
      background-image: url("../../../public/shop_img/shake-hand.png"); /*    */
      background-size: contain; /*    */
      background-repeat: no-repeat; /*   */
      margin: 0 auto;
      height: 70px; /*    */
      width: 70px; /*    */
    }
  }
}

//  
@media (max-width: 400px) {
  .mainSection {
    height: auto; /*     */

    .imgSection {
      height: 50% !important; /*    */
      max-height: 400px;
      background-size: contain;
      background-image: url(../../../public/shop_img/shop_img_phone.png) !important; //   
    }

    .manageShopBtn {
      top: 5px; /*    */
      right: 5px; /*    */
    }
  }
}

@media (max-height: 850px) {
  .mainSection {
    .imgSection {
      height: 65%; /*       */
    }
  }
}

//  
@media (max-width: 400px) {
  .sliderContainer {
    height: 120px; //    
  }

  .slider {
    width: calc(200px * 5 * 2); //    
  }

  .slideImgBox {
    width: 200px; //     

    .slide {
      &.slide1,
      &.slide2,
      &.slide3,
      &.slide4,
      &.slide5 {
        width: 45px; //   
        height: 40px; //   
        margin-right: 10px; //   
      }
    }
  }

  .slideText {
    padding-top: 5px; //    
    margin-right: 5px; //   
  }
}

@media (max-height: 850px) {
  .sliderContainer {
    height: 100px; //       
  }

  .slideImgBox {
    .slide {
      &.slide1,
      &.slide2,
      &.slide3,
      &.slide4,
      &.slide5 {
        height: 35px; //   
      }
    }
  }
}

//  
@media (max-width: 400px) {

  :root {
    --img-section-display: none; //   
  }

  .imgSection2 {
    height: 50% !important; /*    */
    max-height: 400px;
    background-size: contain;
    background-image: url(../../../public/shop_img/shop_img_phone2.png) !important; //   
  }

  .imgSection3Box {
    flex-direction: column; /*    */
  }

  .imgSection3 {
  display: var(--img-section-display); //   display 
  }

  .processContainer {
    width: 100% !important; //    

    .content {
      margin-top: 20px; //   

      .guide {
        width: 90%; //   
        margin-left: auto; //  
        margin-right: auto; //  
      }

      .selectDogBox {
        .dogList {
          width: 100%; //   
          margin: 0 auto !important;
        }
      }

      .startBtnBox {
        .startButton {
          width: 100%; //    
          margin-left: auto !important; //  
          margin-right: auto !important; //  
          margin-bottom: 20px;
        }
      }
    }
  }
}

//  
@media (max-width: 400px) {
  :root {
    --padding-left: 30px;
    --padding-right: 30px;
    --padding-top: 20px;
  }

  .processContent {
    padding-left: var(--padding-left);
    padding-right: var(--padding-right);
    padding-top: var(--padding-top);
  }

  .processTitle {
    font-size: 28px; /*     */
    margin-bottom: 20px; /*   */
  }

  .processStep {
    flex-direction: column; /*    */

    .stepNumber {
      margin-bottom: 10px; /*   */
    }

    .stepContent {
      h5 {
        font-size: 16px; /*     */
      }

      p {
        font-size: 14px; /*     */
      }
    }
  }

  .featuresContainer {
    flex-direction: column; /*    */
    gap: 25px; /*     */
    padding: 20px 0; /*    */
  }

  .featureItem {
    max-width: 90%; /*     */
    margin-left: 30px;
  }

  .subsIcon,
  .qualityIcon {
    height: 50px; /*    */
    width: 50px; /*    */
  }
}
