//  SCSS 
@import "~bootstrap/scss/functions"; //  
@import "~bootstrap/scss/variables"; //  
@import "~bootstrap/scss/mixins"; //  
@import "~bootstrap/scss/card"; //   

:root {
  --btn-width: 40%; //    
  --btn-fz: 16px;
  --img-width: 300px;
}

.loadingOverlay {
  position: fixed; //   
  top: 0;
  left: 0;
  width: 100%;
  height: 100%; //     
  background-color: rgba(0, 0, 0, 0.5); //  
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999; //      z-index 
}

//    
.spinnerContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  .loader {
    opacity: 1;
  }
}

.treatsList {
  background-size: cover; //    
  background-position: center; //    
  color: white; //   
  margin-top: 5%;
  margin-bottom: 5%;

  //   
  .cardContainer {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: flex-start;
    gap: 15px;
    margin: 0 auto;

    .card {
      width: calc(
        (100% - 30px) / 3
      ); //   30px(10px  * 2)  3 
      margin-bottom: 20px; //  
      box-sizing: border-box; //   

      //  
      @extend .card;
      background: white;
      border-radius: 10px; //  

      .cardBody {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        height: 100%;
      }

      .cardTitle {
        font-size: 15px;
        flex-grow: 1;
        padding: 0 20px;
        color: #403c36;
        cursor: pointer; //   
      }
    }

    .cardImageTop {
      border-top-left-radius: 10px; //  
      border-top-right-radius: 10px; //  
      width: 100%; //    
      height: var(--img-width); //   
      min-height: var(--img-width); //   
      object-fit: cover; //    
      cursor: pointer; //   
    }

    .addBtnContainer {
      display: flex;
      margin-top: auto;
      margin-bottom: 10px;
      justify-content: center;

      .addBtn {
        background-color: #e88b4f; //  
        width: var(--btn-width); //    
        color: white; // 
        border: none; //  
        border-radius: 25px; //  
        padding: 12px 20px; // 

        cursor: pointer; //   
        font-size: var(--btn-fz);
        transition: background-color 0.3s, transform 0.2s; //  

        &:hover {
          transform: scale(1.05); //    
        }

        &:active {
          transform: scale(0.95); //    
        }
      }
    }
  }

  .content {
    display: flex;
    flex-direction: column; //   
    background-color: #f4e8d3; //  
    padding: 40px; // 
    border-radius: 25px; //  
    width: 60%;
    margin: 0 auto; //  
    box-sizing: border-box; //   
    color: #403c36; //  
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); //  

    h1 {
      font-size: 35px;
      text-align: center; //   
      margin-top: 0px;
      margin-bottom: 10px;
    }

    .subTitle {
      text-align: center; //   
      font-size: 14px;
      margin: 0px 0 30px; //    
    }
  }

  .treat {
    border: 1px solid #ccc; // 
    padding: 20px; // 
    margin-bottom: 20px; //  
    border-radius: 5px; //  
    background-color: rgba(255, 255, 255, 0.8); //   
  }

  .loading,
  .error {
    text-align: center; //  
    margin-top: 20px; //  

    &.error {
      color: red; //   
    }
  }

  .treatList {
    display: block; //   
    margin: 0 auto 20px; //  
  }

  .buttonContainer {
    text-align: center; //   
    margin-top: 0px; //     
  }
}

.selectedTreats {
  position: sticky; //  
  bottom: 20px; //   
  left: 50%; //   
  transform: translateX(-50%); //    
  width: 80%; //  
  max-width: 30vw; //   
  opacity: 0.9;
  margin: 0 auto; //  
  padding: 0px;
  border: 1px solid #e0e0e0;
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.95);
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  color: #333;
  z-index: 2000; //     

  .imageBoxContainer {
    display: flex; // flexbox 
    flex-wrap: wrap; //   
    justify-content: center; //  
  }

  .imageBox {
    position: relative;
    width: 90px; //   
    height: 90px; //   
    margin: 10px; //   
    border: 1px solid #ccc; //  
    border-radius: 5px; // 
    overflow: hidden; //    

    .removeBtn {
      position: absolute;
      top: 2px;
      right: 0px;
      background: none;
      border: none;
      color: #e0676c;
      font-size: 25px;
      cursor: pointer;
      display: none; //  

      &:hover {
        color: rgb(216, 18, 18);
      }
    }

    &:hover {
      .removeBtn {
        display: block; //    
      }
    }
  }

  @keyframes slideIn {
    from {
      transform: translateY(-60px);
      opacity: 0;
    }
    to {
      transform: translateY(0);
      opacity: 1;
    }
  }

  @keyframes slideOut {
    from {
      transform: translateY(0);
      opacity: 1;
    }
    to {
      transform: translateY(-60px); //   
      opacity: 0;
    }
  }

  .treatWrapper {
    animation: slideIn 0.8s ease forwards; //  
  }

  .slideOut {
    animation: slideOut 1s ease forwards; //   
  }

  .treatImage {
    width: 100%; //    
    opacity: 0.8;
    height: auto; //  
  }
}

.page-enter {
  opacity: 0;
}
.page-enter-active {
  opacity: 1;
  transition: opacity 500ms;
}
.page-exit {
  opacity: 1;
}
.page-exit-active {
  opacity: 0;
  transition: opacity 500ms;
}

@media (max-width: 400px) {
  :root {
    --btn-width: 90%; //    
    --btn-fz: 12px;
  }

  .treatsList {
    display: flex; // Flexbox 
    flex-direction: column; //   
    align-items: center; //   
    margin: 0 auto;

    .card {
      width: calc((100%)); // 2  
    }

    .content {
      h1 {
        font-size: 24px;
      }
      width: 90%; //   
    }
  }

  .addBtn {
    width: var(--btn-width); //    
    font-size: var(--btn-fz);
  }

  .selectedTreats {
    max-width: 95vw; //   
    display: flex; // Flexbox 
    flex-direction: row; //   
    justify-content: center; //   
    align-items: center; //   
    margin-left: 200px;

    .imageBox {
      width: 40px; //    
      height: 40px; //    
    }
  }
}

