.createPostPage {
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 100vh;
    background-color: #F1E1C8;
    padding: 20px;
    // font-family: 'Arial', sans-serif;
    max-width: 1000px; //   1000px 
    margin: 0 auto;
}

.headerNav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    max-width: 700px; //    
    margin-bottom: 20px;
    padding: 0; //  
}

.backButton,
.homeButton {
    font-size: 24px; //    
    color: #333;
    background: none;
    border: none;
    cursor: pointer;
    padding: 5px; //  
    transition: transform 0.3s ease;

    &:hover {
        transform: scale(1.1);
    }
}

.postTitle {
    font-size: 28px; //    
    font-weight: bold;
    margin-bottom: 20px;
    color: #333;
    text-align: center;
    cursor: pointer;
}

.form {
    width: 100%;
    max-width: 700px; //    
    background-color: #fff;
    padding: 25px; //    
    border-radius: 12px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.input,
.textarea {
    width: 100%;
    padding: 12px; //     
    margin-bottom: 15px;
    border: 1px solid #ddd;
    border-radius: 8px;
    font-size: 15px; //    
    transition: border-color 0.3s;
    box-sizing: border-box;

    border: 3px solid #cfbbbb;
        background: none;
        padding: 1rem;
        font-size: 1rem;
}

.textarea {
    min-height: 180px; //     
}

.imageLabel {
    display: inline-block;
    padding: 10px 18px; //   
    background-color: #f0f0f0;
    color: #333;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.3s;
    font-size: 15px; //    

    &:hover {
        background-color: #e0e0e0;
    }
}

.submitButton {
    width: 100%;
    padding: 12px; //   
    background-color: #B3797A;
    color: white;
    border: none;
    border-radius: 8px;
    font-size: 16px; //    
    font-weight: bold;
    cursor: pointer;
    transition: background-color 0.3s;

    &:hover {
        background-color: #9F5A5B;
    }
}

@media (max-width: 768px) {
    .createPostPage {
        padding: 10px;
    }

    .headerNav {
        max-width: 100%;
    }

    .postTitle {
        font-size: 22px;
    }

    .form {
        padding: 15px;
    }

    .input,
    .textarea,
    .submitButton {
        font-size: 14px;

            border: 3px solid #cfbbbb;
                background: none;
                padding: 1rem;
                font-size: 1rem;
               
    }
}

.imagePreview{
    display: flex;
    flex-wrap: wrap;/*    */
    margin-top: 10px;
    margin-bottom:10px;
}
.imageItem{
    margin-right: 10px;
    margin-bottom: 10px;
        /*    */
}

.imageUpload {
    background-color: #f0f0f0; //  
    transition: background-color 0.3s; //   
    margin-bottom: 10px;
}

.imageUpload.hasImages {
    background-color: #8EBC8F; //     
}

.imageLabel {
    color: gray; //  
    color: #FFFFFF; // 
}

.imageLabelActive {
     //    
    background-color: #218838; // 
        color: #FFFFFF; // 
}

.imageLabelInactive {
    background-color: gray; //    
}