.modalOverlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 3000;
}

.modalContent {
  position: fixed; /*   */
  background-color: white;
  border-radius: 8px;
  padding: 20px;
  min-width: 70%;
  max-width: 70%;
  max-height: 800px; /*     */
  overflow-y: auto; /*    */
  left: 50%; /*   */
  transform: translate(-50%, 0); /*   ,   JavaScript  */
}

.closeButton {
  font-size: 32px;
  color: rgb(175, 48, 48);
  position: absolute;
  top: 5%;
  right: 3%;
  background: none;
  border: none;
  cursor: pointer;
}
