.reviewList {
  width: 100%;
  max-width: 1300px;
  margin: 50px auto;

  .swiper {
      width: 100%;
      height: 100%;
  }

  .swiper-slide {
      display: flex;
      justify-content: center;
      align-items: center;
      box-sizing: border-box;
      width: calc(50% - 20px); //   
      padding: 10px;
  }

  .review {
      background: #F4E8D3;
      border-radius: 8px;
      padding: 20px;
      box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
      color: #5C4033;
      width: 100%; 
      max-width: 600px;
  }

  .reviewContent {
      font-size: 16px;
      margin-bottom: 10px;
  }

  .reviewDetails {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
  }

  .reviewRate {
      display: flex;
      align-items: center;
      margin-bottom: 5px;
  }

  .reviewDate,
  .reviewUser {
      font-size: 14px;
      color: #5C4033;
      margin-bottom: 3px;
  }

  .starIcon {
      color: #ffc107;
      margin-right: 5px;
  }

  .swiperButtonPrev,
  .swiperButtonNext {
      color: #860e0e !important;
  }

}

@media (max-width: 400px) {
  .swiper-slide {
      width: 90%; // 400px       
      padding: 0; //    
  }

  .review {
      padding: 15px; //    
      margin-left: 30px;
      max-width: 300px !important;//    
  }
  .swiperButtonNext {
    display: none; // 400px    
  }
}

.reviewLoading {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 40px;
  height: 100px; /*   */
  font-size: 18px; /*    */
  color: #5C4033; /*    */
  text-align: center; /*    */
  width: 100%; /*     */
}